import React from 'react'
import 'intersection-observer'
import { useInView } from 'react-intersection-observer'
import {Container, Row, Col} from 'react-bootstrap'

function ContactMainContent () {
    const tshld = 0.

    const [ref1, inView1] = useInView({ threshold: tshld })
    const [ref2, inView2] = useInView({ threshold: tshld })
    const [ref3, inView3] = useInView({ threshold: tshld })
    const [ref4, inView4] = useInView({ threshold: tshld })

    const isVis1 = inView1 ? 'animate__animated animate__fadeIn animate__fast' : 'animate__animated animate__fadeOut animate__fast'
    const isVis2 = inView2 ? 'animate__animated animate__fadeIn animate__fast' : 'animate__animated animate__fadeOut animate__fast'
    const isVis3 = inView3 ? 'animate__animated animate__fadeIn animate__fast' : 'animate__animated animate__fadeOut animate__fast'
    const isVis4 = inView4 ? 'animate__animated animate__fadeIn animate__fast' : 'animate__animated animate__fadeOut animate__fast'

    return (
        <div>
            <Container fluid={true} className="contact">
                <Row>
                    <Col className="title animate__animated animate__fadeInUp">
                        <h1>Let's Connect!</h1>
                    </Col>
                </Row>
                <Row>
                    <Col ref={ref1} className={`${isVis1} box1 contact-item`} md={false} lg={true}>
                        <img src="https://static.thenounproject.com/png/437813-200.png" alt="not found"/>
                        <h2>davis.liu@uwaterloo.ca</h2> 
                        <h2>kfdliu@gmail.com</h2>
                    </Col>
                    <Col ref={ref2} className={`${isVis2} box2 contact-item`} md={false} lg={true}>
                        <img src="https://upload.wikimedia.org/wikipedia/commons/6/6c/Phone_icon.png" alt="not found"/>
                        <h2>(437) 772-2620</h2>
                    </Col>
                </Row>
                <Row >
                    <Col ref={ref3} className={`${isVis3} box3 contact-item`} md={false} lg={true}>
                        <img src="https://cdn.iconscout.com/icon/free/png-512/linkedin-1796350-1524231.png" alt="not found"/>
                        <h2><a href="https://www.linkedin.com/in/kfdliu" target="_blank">linkedin.com/in/kfdliu</a></h2>
                    </Col>
                    <Col ref={ref4} className={`${isVis4} box4 contact-item`} md={false} lg={true}>
                        <img src="https://cdn.iconscout.com/icon/free/png-512/github-163-761603.png" alt="not found"/>
                        <h2><a href="https://github.com/kfdliu" target="_blank">github.com/kfdliu</a></h2>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default ContactMainContent